}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.backdrop {
  opacity: 1;
  transition: opacity var(--animation), visibility var(--animation);
  z-index: 2;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .2);
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.is-hidden .modal {
  opacity: 0;
  transform: translate(-50%, -50%)scale(.3);
}

.modal {
  opacity: 1;
  transition: opacity var(--animation), transform var(--animation);
  max-width: 100%;
  width: 440px;
  font-family: var(--main-font-family);
  color: var(--primary-text-color);
  background-color: var(--buttons-modal-bg-color);
  margin-left: auto;
  margin-right: auto;
  padding: 48px 20px 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.16667;
  position: absolute;
  top: 100px;
  left: 50%;
  transform: translate(-50%)scale(1);
}

@media screen and (min-width: 768px) {
  .modal {
    width: 704px;
    padding: 40px 72px 64px 36px;
  }
}

@media screen and (min-width: 1280px) {
  .modal {
    width: 806px;
    padding: 40px 12px;
  }
}

.modal__close {
  width: 30px;
  height: 30px;
  background-color: inherit;
  cursor: pointer;
  transition: fill var(--animation), background-color var(--animation);
  border-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 8px;
  right: 8px;
}

.modal__close:hover, .modal__close:focus {
  background-color: var(--buttons-modal-text-color);
  fill: var(--accent-text-color);
}

.modal__close:active {
